.wrapper-header {
  @include mp-reset;
  margin: 0 auto;
  padding: 0 12px;
  min-height: 878px;
  background-image: url("../img/device@1x.png"), url("../img/header@1x.jpg");
  //background-image: url("../../img/device.png");
  background-repeat: no-repeat, no-repeat;
  background-position: calc(450px + 48.3%) 260px, 50% 0;
  background-size: 705px 515px, 1920px 881px;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/device@2x.png"), url("../img/header@2x.jpg");
  }
}

@media (max-width: $tablet-width) {
  .wrapper-header {
    min-height: 686px;
    padding: 0 25px;
    background-image: url("../img/component-table@1x.jpg");
    background-repeat: no-repeat;
    background-position: 50% 0;
    background-size: cover;
    background-color: transparent;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/component-table@2x.jpg");
    }
  }
}

@media (max-width: $mobile-width) {
  .wrapper-header {
    min-height: 647px;
    padding: 0 16px;
    background-image: url("../img/component-mobile@1x.jpg");
    background-repeat: no-repeat;
    background-position: 50% 18%;
    background-size: cover;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/component-mobile@2x.jpg");
    }
  }
}

.wrapper-advantages {
  @include mp-reset;
  margin: 0 auto;
  box-sizing: border-box;
  min-height: 1263px;
  background-image: url("../img/advantages@1x.jpg");
  background-repeat: no-repeat;
  background-position: 50% -30px;
  background-size: 1920px 1347px;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/advantages@2x.jpg");
  }
}

@media (max-width: $tablet-width) {
  .wrapper-advantages {
    min-height: 1108px;
    background-image: url("../img/advantages-tablet@1x.jpg");
    background-repeat: no-repeat;
    background-position: 100% 100px;
    background-size: 559px 853px;
    background-color: $bg-white;
    //background-color: transparent;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/advantages-tablet@2x.jpg");
    }
  }
}

@media (max-width: $mobile-width) {
  .wrapper-advantages {
    min-height: 1797px;
    background-image: url("../img/advantages-mobile@1x.jpg");
    background-repeat: no-repeat;
    background-position: 100% 650px;
    background-size: 320px 873px;
    background-color: $bg-white;
    //background-color: transparent;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/advantages-mobile@2x.jpg");
    }
  }
}

.wrapper-about-service {
  @include mp-reset;
  box-sizing: border-box;
  margin: 0 auto;
  max-width: 1198px;
  padding: 0 12px;
  padding-top: 59px;
  min-height: 938px;
  background-color: $bg-black;
  //background: transparent;
}

@media (max-width: $tablet-width) {
  .wrapper-about-service {
    min-height: 1127px;
    padding: 0 24px;
  }
}

@media (max-width: $mobile-width) {
  .wrapper-about-service {
    min-height: 1101px;
    padding: 0 15px;
    /* Стили, влияющие на экраны ДО 767px (Мобильные) */
  }
}

.wrapper-support {
  @include mp-reset;
  margin: 0 auto;
  padding: 0 12px;
  min-height: 667px;
  background-image: url("../img/paper_airplane@1x.png"), url("../img/support@1x.jpg");
  //background-image: url("../../img/paper_airplane@1x.png");
  background-repeat: no-repeat, no-repeat;
  background-position: calc(50% - 354px) 155px, 50% -60px;
  background-size: 511px 288px, 1920px 760px;
  //background-color: $bg-blue;
  background-color: transparent;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/paper_airplane@1x.png"), url("../img/support@2x.jpg");
  }
}

@media (max-width: $tablet-width) {
  .wrapper-support {
    /* Стили, влияющие на экраны ДО 1023px (Планшет) */
    background-image: none;
    min-height: 664px;
    background-color: $bg-blue;
  }
}

@media (max-width: $mobile-width) {
  .wrapper-support {
    /* Стили, влияющие на экраны ДО 767px (Мобильные) */
    min-height: 640px;
  }
}

.wrapper-footer {
  @include mp-reset;
  margin: 0 auto;
  max-width: 1416px;
  padding: 0 12px;
  padding-top: 68px;
  min-height: 190px;
  background-color: $bg-white;
  display: flex;
  justify-content: space-between;
}

@media (max-width: $tablet-width) {
  .wrapper-footer {
    /* Стили, влияющие на экраны ДО 1023px (Планшет) */
    box-sizing: border-box;
    min-height: 374px;
    margin: 0;
    padding: 0;
    width: 100%;
    flex-direction: column;
    justify-content: start;
  }
}

@media (max-width: $mobile-width) {
  .wrapper-footer {
    /* Стили, влияющие на экраны ДО 767px (Мобильные) */
    min-height: 372px;
    padding: 0;
  }
}


.wrapper-footer-footer {
  @include mp-reset;
  margin: 0 auto;
  max-width: 1412px;
  padding: 0 12px;
  min-height: 29px;
  padding-top: 13px;
  background-color: $bg-white;
}

@media (max-width: $tablet-width) {
  .wrapper-footer-footer {
    /* Стили, влияющие на экраны ДО 1023px (Планшет) */
    min-height: 68px;
    padding: 0;
    margin: 28px 26px 0 26px;
    background-color: $bg-white;
  }
}

@media (max-width: $mobile-width) {
  .wrapper-footer-footer {
    /* Стили, влияющие на экраны ДО 767px (Мобильные) */
    min-height: 104px;
    margin: 0 auto;
  }
}
